import React from "react";
import { GET_MEDIA_NAME } from "../../../utils";
import { styleHeaderBanner } from "./Elements/HeaderConstant";
import { CloudHostUrl, uploadedUrl } from "../../../constants";
const HeaderType3 = ({ isDisplay, Banner }) => {
  const { bannerStyle, subTitleStyle, titleStyle, iconAreaStyle, iconStyle, isFacebook, isTwitter, isInstagram, facebookIconAreaStyle, facebookIconStyle, twitterIconAreaStyle, twitterIconStyle, instagramIconAreaStyle, instagramIconStyle, subTitleVarentClass, titleVarentClass } = styleHeaderBanner(Banner);
  var bLogoImg = Banner.LogoImage;
  if (bLogoImg && String(bLogoImg).length > 0 && String(bLogoImg).includes("banner/logo_bnr") && String(bLogoImg).includes(uploadedUrl)) bLogoImg = String(Banner.LogoImage).replace(uploadedUrl, "")
  else if (bLogoImg.includes(uploadedUrl) && bLogoImg.split(uploadedUrl)[1] === '') bLogoImg = String(Banner.LogoImage).replace(uploadedUrl, "")
  else if (bLogoImg && String(bLogoImg).length > 0) {
    let LogoImage = bLogoImg.split('/').pop();
    if (LogoImage == 'logo_bnr-2.svg' || LogoImage == 'logo_bnr-3.svg' || LogoImage == 'logo_bnr-5.svg') {
      bLogoImg = `${CloudHostUrl}react-app/media/banner/logo_bnr-3.svg`
    }
  }
  var bCustomStyle = bannerStyle;
  if (bCustomStyle && bCustomStyle.backgroundImage.includes("bg-bnr-3.png") && bCustomStyle.backgroundImage.includes(uploadedUrl)) bCustomStyle.backgroundImage = bCustomStyle.backgroundImage.replace(uploadedUrl, "https://test.taggbox.com/widget/assets/media/banner/")
  return <div className="tb_wall_header_wrap__" style={bCustomStyle}>
    <div className="tb_header3_row__">
      <div className="tb_header3_column_logo__">
        {bLogoImg ?
          <div className="tb_header3_logo__">
            <img loading="lazy" className="tb_header3_logo_img__" src={bLogoImg} alt={GET_MEDIA_NAME(bLogoImg)} width={180} height={52} />
          </div>
          : ''}
      </div>
      <div className="tb_header3_column_content__">
        <div className="tb_header3_content__">
          <div className={`tb_header3_subtitle__ tb-cTBfont-${subTitleVarentClass}`} style={subTitleStyle}>{Banner.subTitle}</div>
          <div className={`tb_header3_title__ tb-cTBfont-${titleVarentClass}`} style={titleStyle}>{Banner.title}</div>
        </div>
      </div>
      <div className="tb_header3_column_icons__">

        {Banner.social_icon_status === 1 ? <div className="tb_header3_social_icons__">
          {isFacebook ?
            <a onClick={() => !isDisplay ? Banner.facebook_url && window.open(Banner.facebook_url, "_blank") : null } className="tb_header3_social_icon_list__" aria-label="Facebook" style={{ ...iconAreaStyle, ...facebookIconAreaStyle }} rel="noopener noreferrer">
              <div className={`tb_header3_social_icon__ tb__icon tb-facebook`} style={{ ...iconStyle, ...facebookIconStyle }}> </div>
            </a> : null}
          {isTwitter ?
            <a onClick={() => !isDisplay ? Banner.twitter_url && window.open(Banner.twitter_url, "_blank") : null } className="tb_header3_social_icon_list__" aria-label="Twitter" style={{ ...iconAreaStyle, ...twitterIconAreaStyle }} rel="noopener noreferrer">
              <div className="tb_header3_social_icon__ tb__icon tb-twitter" style={{ ...iconStyle, ...twitterIconStyle }}></div>
            </a> : ''}
          {isInstagram ?
            <a onClick={() => !isDisplay ? Banner.instagram_url && window.open(Banner.instagram_url, "_blank") : null } className="tb_header3_social_icon_list__" aria-label="Instagram" style={{ ...iconAreaStyle, ...instagramIconAreaStyle }} rel="noopener noreferrer">
              <div className="tb_header3_social_icon__ tb__icon tb-instagram" style={{ ...iconStyle, ...instagramIconStyle }}> </div>
            </a> : ''}
        </div> : null
        }</div>
    </div>
  </div>
}
export default HeaderType3